 *{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
 }
 

.header-left-bg{
   background:  linear-gradient(to bottom, rgba(0, 0, 0, 0.897), rgba(0, 0, 0, 0.877)),  url('./components/assets/header.png');
   background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
 
  background-size: contain, cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
 }
.wraper{
  object-fit: contain;
}
.ff-bg-vec{
  background:   url('./components/assets/ffvec.svg');
  background-position: center center;
 background-repeat: no-repeat;
 overflow: hidden;
 width: 100%;
 background-size: contain, cover;
 -webkit-background-size: cover;
 -moz-background-size: cover;
 -o-background-size: cover;
 background-size: cover;
}
#loading {
   animation: rotation 2s infinite linear;
}

@keyframes rotation {
  from {
      -webkit-transform: rotate(0deg);
  }
  to {
      -webkit-transform: rotate(359deg);
  }
}

 
@keyframes xxx {
  from { width: 0%; }
  to { width:100%; }
}

.wrapper {
  width: 100%;
}

.bar {
  width: 100%;
  
}

.is-loading {
  animation: xxx  ;
}

.is-done {
  transition: width 1s;
  width: 100% !important;
}


 
 
 

@keyframes qq {
  0% {
      transform: scaleY(0%);
      opacity: 0;
  }
  100% {
      transform: scaleY(100%);
      opacity: 1;
  }
}

@keyframes aa {
  0% {
      transform: scaleY( 10%);
  }
  100% {
      transform: scaleY(-100%);
  }
}

.barrr {
 
 
  background: gray;
  
}

.is-loadinggg {
  animation: qq;
}

.is-done {
  transition: width 1s;
  width: 100% !important;
}







